/**
 * CollectionThumbnail.tsx
 * Displays a thumbnail for a collection of videos
 */

import React from 'react';

interface CollectionThumbnailProps {
  name: string;
  subtitle: string;
  tagline?: string; 
  videoCount: number;
  color: string;
  onClick?: () => void;
}

export default function CollectionThumbnail({
  name,
  subtitle,
  tagline,
  videoCount,
  color,
  onClick
}: CollectionThumbnailProps) {
  return (
    <div 
      className="relative aspect-[4/3] w-full rounded-2xl overflow-hidden cursor-pointer shadow-lg group" 
      onClick={onClick}
    >
      {/* Background with gradient */}
      <div className={`absolute inset-0 ${color}`}></div>
      
      {/* Pattern overlays */}
      <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,')] bg-center opacity-5"></div>
      <div className="absolute top-0 right-0 w-48 h-48 bg-white opacity-5 rounded-full -mr-16 -mt-16"></div>
      <div className="absolute bottom-0 left-0 w-48 h-48 bg-black opacity-10 rounded-full -ml-16 -mb-16"></div>
      
      {/* Logo watermark */}
      <div className="absolute bottom-4 right-4 text-white/30 text-xs font-light">
        growlink.ag
      </div>
      
      {/* Chevron design element */}
      <div className="absolute top-6 left-0 w-full">
        <svg viewBox="0 0 100 20" className="w-full opacity-20">
          <path d="M0 20 L50 0 L100 20" fill="none" stroke="white" strokeWidth="1" />
          <path d="M0 15 L50 0 L100 15" fill="none" stroke="white" strokeWidth="0.5" />
        </svg>
      </div>
      
      {/* Title content */}
      <div className="absolute inset-0 p-8 flex flex-col justify-center items-center text-center">
        <h2 className="text-3xl font-bold text-white mb-2">
          {name.split(' ')[0]}
        </h2>
        <p className="text-xl text-white/80 font-medium leading-tight">
          {subtitle}
        </p>
        {tagline && (
          <p className="text-white/70 text-sm mt-1">{tagline}</p>
        )}
      </div>
      
      {/* Videos count badge */}
      <div className="absolute bottom-4 left-4 px-3 py-1 bg-white/20 backdrop-blur-sm rounded-full text-sm text-white">
        {videoCount} videos
      </div>
    </div>
  );
}