/**
 * VideoGrid.tsx
 * Displays a grid of videos with filtering options
 */

import React, { useState, useEffect, useMemo } from 'react';
import { Search, Filter, X } from 'lucide-react';
import VideoCard from './VideoCard';
import VideoModal from './VideoModal';
import { VideoData } from '../../services/VimeoService';

interface VideoGridProps {
  videos: VideoData[];
  categories?: string[];
  collections?: string[];
}

type FilterType = 'category' | 'collection' | 'duration';

export default function VideoGrid({ videos, categories = [], collections = [] }: VideoGridProps) {
  const [selectedVideo, setSelectedVideo] = useState<VideoData | null>(null);
  const [activeCategory, setActiveCategory] = useState<string | null>(null);
  const [activeCollection, setActiveCollection] = useState<string | null>(null);
  const [searchQuery, setSearchQuery] = useState<string>('');
  const [durationFilter, setDurationFilter] = useState<string | null>(null);
  const [showFilters, setShowFilters] = useState<boolean>(true);
  const [activeFilterType, setActiveFilterType] = useState<FilterType>('category');
  
  // Extract unique duration ranges
  const durationRanges = useMemo(() => {
    const ranges = [
      { label: 'Short (< 3 min)', value: 'short', check: (duration: string) => {
        const minutes = parseInt(duration.split(':')[0]);
        return minutes < 3;
      }},
      { label: 'Medium (3-10 min)', value: 'medium', check: (duration: string) => {
        const minutes = parseInt(duration.split(':')[0]);
        return minutes >= 3 && minutes <= 10;
      }},
      { label: 'Long (> 10 min)', value: 'long', check: (duration: string) => {
        const minutes = parseInt(duration.split(':')[0]);
        return minutes > 10;
      }}
    ];
    return ranges;
  }, []);

  // Reset filters when categories or collections change
  useEffect(() => {
    setActiveCategory(null);
    setActiveCollection(null);
    setDurationFilter(null);
    setSearchQuery('');
  }, [categories, collections]);

  // Get unique categories and collections from videos if not provided
  const derivedCategories = useMemo(() => {
    if (categories.length > 0) return categories;
    return Array.from(new Set(videos.map(video => video.category)));
  }, [videos, categories]);
  
  const derivedCollections = useMemo(() => {
    if (collections.length > 0) return collections;
    return Array.from(
      new Set(videos
        .filter(video => video.collection && typeof video.collection === 'string')
        .map(video => video.collection as string))
    );
  }, [videos, collections]);

  // Apply filters based on active filters and search query
  const filteredVideos = useMemo(() => {
    return videos.filter(video => {
      // Filter by search query
      const matchesSearch = !searchQuery || 
        video.title.toLowerCase().includes(searchQuery.toLowerCase()) ||
        video.description.toLowerCase().includes(searchQuery.toLowerCase());
      
      // Filter by category if active
      const matchesCategory = !activeCategory || video.category === activeCategory;
      
      // Filter by collection if active
      const matchesCollection = !activeCollection || video.collection === activeCollection;
      
      // Filter by duration if active
      const matchesDuration = !durationFilter || 
        durationRanges.find(range => range.value === durationFilter)?.check(video.duration);
      
      // Video must match all active filters
      return matchesSearch && matchesCategory && matchesCollection && matchesDuration;
    });
  }, [videos, searchQuery, activeCategory, activeCollection, durationFilter, durationRanges]);

  // Reset all filters
  const resetFilters = () => {
    setActiveCategory(null);
    setActiveCollection(null);
    setDurationFilter(null);
    setSearchQuery('');
  };

  // Check if any filter is active
  const hasActiveFilters = activeCategory !== null || activeCollection !== null || durationFilter !== null || searchQuery !== '';
  
  // Count videos in each category, collection, and duration range
  const categoryCounts = useMemo(() => {
    const counts: Record<string, number> = {};
    derivedCategories.forEach(category => {
      counts[category] = videos.filter(video => video.category === category).length;
    });
    return counts;
  }, [videos, derivedCategories]);
  
  const collectionCounts = useMemo(() => {
    const counts: Record<string, number> = {};
    derivedCollections.forEach(collection => {
      counts[collection] = videos.filter(video => video.collection === collection).length;
    });
    return counts;
  }, [videos, derivedCollections]);
  
  const durationCounts = useMemo(() => {
    const counts: Record<string, number> = {};
    durationRanges.forEach(range => {
      counts[range.value] = videos.filter(video => range.check(video.duration)).length;
    });
    return counts;
  }, [videos, durationRanges]);

  return (
    <div className="relative">
      {/* Search and filter bar */}
      <div className="sticky top-0 z-20 bg-white pt-2 pb-4 mb-8">
        <div className="flex flex-col md:flex-row md:items-center gap-4">
          {/* Search input */}
          <div className="relative flex-grow">
            <div className="absolute inset-y-0 left-0 pl-4 flex items-center pointer-events-none">
              <Search className="h-4 w-4 text-neutral-400" />
            </div>
            <input
              type="text"
              placeholder="Search tutorials..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="block w-full pl-10 pr-3 py-2.5 border-0 bg-neutral-100 rounded-full text-sm focus:outline-none focus:ring-1 focus:ring-primary focus:bg-white transition-colors"
            />
            {searchQuery && (
              <button
                onClick={() => setSearchQuery('')}
                className="absolute inset-y-0 right-0 pr-4 flex items-center"
              >
                <X className="h-3.5 w-3.5 text-neutral-400 hover:text-neutral-600" />
              </button>
            )}
          </div>
          
          {/* Filter button */}
          <div className="flex items-center">
            <button
              onClick={() => setShowFilters(!showFilters)}
              className="flex items-center gap-1.5 px-4 py-2.5 text-sm text-neutral-700 hover:text-primary rounded-full bg-neutral-100 hover:bg-neutral-200 transition-colors"
            >
              <Filter className="h-3.5 w-3.5" />
              <span>Filters</span>
              {hasActiveFilters && (
                <span className="ml-1 w-5 h-5 flex items-center justify-center text-xs bg-orange-500 text-white rounded-full">
                  {(activeCategory ? 1 : 0) + (activeCollection ? 1 : 0) + (durationFilter ? 1 : 0)}
                </span>
              )}
            </button>
            
            {hasActiveFilters && (
              <button
                onClick={resetFilters}
                className="ml-2 flex items-center text-sm text-neutral-500 hover:text-neutral-800 transition-colors"
              >
                <span>Clear</span>
              </button>
            )}
          </div>
        </div>
        
        {/* Results count */}
        <div className="mt-3 text-xs text-neutral-500">
          Showing {filteredVideos.length} of {videos.length} videos
        </div>
      </div>
      
      {/* Category tabs */}
      {showFilters && (
        <div className="mb-6 space-x-4">
          <button
            onClick={() => setActiveFilterType('category')}
            className={`py-2 text-sm font-medium border-b-2 ${
              activeFilterType === 'category'
                ? 'text-orange-500 border-orange-500'
                : 'text-neutral-500 border-transparent hover:text-neutral-800 hover:border-neutral-200'
            }`}
          >
            Categories
          </button>
          
          <button
            onClick={() => setActiveFilterType('collection')}
            className={`py-2 text-sm font-medium border-b-2 ${
              activeFilterType === 'collection'
                ? 'text-orange-500 border-orange-500'
                : 'text-neutral-500 border-transparent hover:text-neutral-800 hover:border-neutral-200'
            }`}
          >
            Collections
          </button>
          
          <button
            onClick={() => setActiveFilterType('duration')}
            className={`py-2 text-sm font-medium border-b-2 ${
              activeFilterType === 'duration'
                ? 'text-orange-500 border-orange-500'
                : 'text-neutral-500 border-transparent hover:text-neutral-800 hover:border-neutral-200'
            }`}
          >
            Duration
          </button>
        </div>
      )}
      
      {/* Category filters */}
      {showFilters && activeFilterType === 'category' && (
        <div className="flex flex-wrap gap-2 mb-8">
          <button
            onClick={() => setActiveCategory(null)}
            className={`px-4 py-1.5 rounded-full text-sm font-medium ${
              activeCategory === null
                ? 'bg-orange-500 text-white shadow-sm'
                : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
            }`}
          >
            All <span className={`${activeCategory === null ? 'text-white/80' : 'text-neutral-500'}`}>({videos.length})</span>
          </button>
          
          {derivedCategories.map(category => (
            <button
              key={category}
              onClick={() => setActiveCategory(category)}
              className={`px-4 py-1.5 rounded-full text-sm font-medium ${
                activeCategory === category
                  ? 'bg-orange-500 text-white shadow-sm'
                  : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
              }`}
            >
              {category} <span className={`${activeCategory === category ? 'text-white/80' : 'text-neutral-500'}`}>({categoryCounts[category]})</span>
            </button>
          ))}
        </div>
      )}
      
      {/* Collection filters */}
      {showFilters && activeFilterType === 'collection' && (
        <div className="flex flex-wrap gap-2 mb-8">
          <button
            onClick={() => setActiveCollection(null)}
            className={`px-4 py-1.5 rounded-full text-sm font-medium ${
              activeCollection === null
                ? 'bg-orange-500 text-white shadow-sm'
                : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
            }`}
          >
            All <span className={`${activeCollection === null ? 'text-white/80' : 'text-neutral-500'}`}>({videos.length})</span>
          </button>
          
          {derivedCollections.map(collection => (
            <button
              key={collection}
              onClick={() => setActiveCollection(collection)}
              className={`px-4 py-1.5 rounded-full text-sm font-medium ${
                activeCollection === collection
                  ? 'bg-orange-500 text-white shadow-sm'
                  : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
              }`}
            >
              {collection} <span className={`${activeCollection === collection ? 'text-white/80' : 'text-neutral-500'}`}>({collectionCounts[collection]})</span>
            </button>
          ))}
        </div>
      )}
      
      {/* Duration filters */}
      {showFilters && activeFilterType === 'duration' && (
        <div className="flex flex-wrap gap-2 mb-8">
          <button
            onClick={() => setDurationFilter(null)}
            className={`px-4 py-1.5 rounded-full text-sm font-medium ${
              durationFilter === null
                ? 'bg-orange-500 text-white shadow-sm'
                : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
            }`}
          >
            All <span className={`${durationFilter === null ? 'text-white/80' : 'text-neutral-500'}`}>({videos.length})</span>
          </button>
          
          {durationRanges.map(range => (
            <button
              key={range.value}
              onClick={() => setDurationFilter(range.value)}
              className={`px-4 py-1.5 rounded-full text-sm font-medium ${
                durationFilter === range.value
                  ? 'bg-orange-500 text-white shadow-sm'
                  : 'bg-neutral-100 text-neutral-700 hover:bg-neutral-200'
              }`}
            >
              {range.label} <span className={`${durationFilter === range.value ? 'text-white/80' : 'text-neutral-500'}`}>({durationCounts[range.value]})</span>
            </button>
          ))}
        </div>
      )}
      
      {/* Active filters */}
      {showFilters && hasActiveFilters && (
        <div className="flex flex-wrap items-center gap-2 mb-6 py-2 border-t border-b border-neutral-100">
          <span className="text-xs text-neutral-500">Active filters:</span>
          
          {activeCategory && (
            <div className="flex items-center gap-1 px-2 py-1 bg-neutral-100 text-neutral-700 rounded-md text-xs">
              <span>Category: <span className="font-medium">{activeCategory}</span></span>
              <button 
                onClick={() => setActiveCategory(null)}
                className="ml-1 text-neutral-400 hover:text-neutral-700"
              >
                <X className="h-3 w-3" />
              </button>
            </div>
          )}
          
          {activeCollection && (
            <div className="flex items-center gap-1 px-2 py-1 bg-neutral-100 text-neutral-700 rounded-md text-xs">
              <span>Collection: <span className="font-medium">{activeCollection}</span></span>
              <button 
                onClick={() => setActiveCollection(null)}
                className="ml-1 text-neutral-400 hover:text-neutral-700"
              >
                <X className="h-3 w-3" />
              </button>
            </div>
          )}
          
          {durationFilter && (
            <div className="flex items-center gap-1 px-2 py-1 bg-neutral-100 text-neutral-700 rounded-md text-xs">
              <span>Duration: <span className="font-medium">
                {durationRanges.find(range => range.value === durationFilter)?.label}
              </span></span>
              <button 
                onClick={() => setDurationFilter(null)}
                className="ml-1 text-neutral-400 hover:text-neutral-700"
              >
                <X className="h-3 w-3" />
              </button>
            </div>
          )}
        </div>
      )}

      {/* No results */}
      {filteredVideos.length === 0 && (
        <div className="py-32 text-center">
          <div className="inline-flex items-center justify-center w-24 h-24 rounded-full bg-neutral-100 mb-6">
            <Search className="h-10 w-10 text-neutral-400" />
          </div>
          <h3 className="text-xl font-medium text-neutral-900 mb-2">No videos found</h3>
          <p className="text-neutral-600 mb-6">
            Try adjusting your search or filter criteria to find what you're looking for.
          </p>
          <button
            onClick={resetFilters}
            className="px-4 py-2 bg-orange-500 text-white rounded-full text-sm"
          >
            Clear all filters
          </button>
        </div>
      )}
      
      {/* Video grid */}
      {filteredVideos.length > 0 && (
        <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4">
          {filteredVideos.map((video, index) => (
            <div
              key={video.id}
              className="animate-[fadeIn_0.3s_ease-in-out]"
              style={{ animationDelay: `${index * 0.05}s` }}
            >
              <VideoCard 
                video={video} 
                onClick={setSelectedVideo} 
              />
            </div>
          ))}
        </div>
      )}
      
      {/* Video modal */}
      {selectedVideo && (
        <VideoModal 
          video={selectedVideo} 
          onClose={() => setSelectedVideo(null)} 
        />
      )}
    </div>
  );
}