/**
 * VideoCard.tsx
 * Displays an individual video card with thumbnail, title, and metadata
 */

import React from 'react';
import { Play } from 'lucide-react';
import StaticThumbnail from './StaticThumbnail';
import { VideoData } from '../../services/VimeoService';

interface VideoCardProps {
  video: VideoData;
  onClick: (video: VideoData) => void;
}

// ALWAYS force use of static thumbnails to ensure consistent appearance
const FORCE_STATIC_THUMBNAILS = true;

export default function VideoCard({ video, onClick }: VideoCardProps) {
  // Determine if we should render a static thumbnail instead of attempting to load from URL
  const shouldUseStaticThumbnail = FORCE_STATIC_THUMBNAILS || 
    !video.thumbnail || 
    video.thumbnail === '' || 
    video.thumbnail.includes('placeholder');
  
  return (
    <div 
      className="group relative cursor-pointer overflow-hidden rounded-2xl bg-white shadow-sm transition-all hover:shadow-md"
      onClick={() => onClick(video)}
    >
      <div className="relative aspect-video w-full overflow-hidden rounded-t-2xl">
        {shouldUseStaticThumbnail ? (
          // Use our static thumbnail generator component
          <StaticThumbnail 
            title={video.title}
            category={video.category}
            collection={video.collection}
            duration={video.duration}
          />
        ) : (
          // Use the actual thumbnail if it's available
          <div className="relative w-full h-full">
            <img
              src={video.thumbnail}
              alt={video.title}
              className="absolute inset-0 h-full w-full object-cover transition-transform duration-300 group-hover:scale-105"
            />
            
            {/* Overlay elements */}
            <div className="absolute inset-0 bg-black bg-opacity-20 transition-opacity duration-300 group-hover:bg-opacity-10" />
            <div className="absolute inset-0 flex items-center justify-center opacity-80 transition-transform duration-300 group-hover:scale-110">
              <div className="flex h-14 w-14 items-center justify-center rounded-full bg-white bg-opacity-90 text-black shadow-lg">
                <Play className="h-6 w-6 fill-current" />
              </div>
            </div>
            <div className="absolute bottom-2 right-2 rounded bg-black bg-opacity-70 px-2 py-1 text-xs text-white">
              {video.duration}
            </div>
          </div>
        )}
      </div>
      
      <div className="p-4">
        <h3 className="text-lg font-medium text-gray-900 line-clamp-1">{video.title}</h3>
        <p className="mt-1 text-sm text-gray-500 line-clamp-2">{video.description}</p>
        <div className="mt-3 flex items-center justify-between">
          <span className="rounded-full bg-gray-100 px-3 py-1 text-xs font-medium text-gray-800">
            {video.category}
          </span>
          <span className="text-xs text-gray-500">{video.date}</span>
        </div>
        {video.collection && (
          <div className="mt-2">
            <span className="rounded-full bg-blue-50 px-3 py-1 text-xs font-medium text-blue-700">
              {video.collection}
            </span>
          </div>
        )}
      </div>
    </div>
  );
}