/**
 * VimeoService.ts
 * Handles fetching and caching of Vimeo video data
 */

export interface VideoData {
  id: string;
  title: string;
  description: string;
  thumbnail: string;
  duration: string;
  category: string;
  date: string;
  vimeoId: string;
  collection?: string;
}

interface VimeoCache {
  lastFetched: number;
  videos: VideoData[];
  expiresAt: number;
}

// Cache expiration time in milliseconds (default: 15 minutes)
const CACHE_EXPIRATION = 15 * 60 * 1000;

// Categories mapping based on keywords in video titles
const categoryKeywords: Record<string, string> = {
  'getting started': 'Setup & Installation',
  'setup': 'Setup & Installation',
  'installation': 'Setup & Installation',
  'climate': 'Climate Control',
  'irrigation': 'Irrigation & Fertigation',
  'fertigation': 'Irrigation & Fertigation',
  'steering': 'Crop Steering',
  'copilot': 'Crop Steering',
  'links': 'LINKS Ecosystem',
  'canopy': 'LINKS Ecosystem',
  'valve': 'Irrigation & Fertigation',
  'hub': 'LINKS Ecosystem',
  'terra': 'LINKS Ecosystem',
  'clima': 'Climate Control',
  'podcast': 'Podcast',
  'customer': 'Customer Stories',
  'nutrient': 'Irrigation & Fertigation',
  'lighting': 'Lighting Control',
};

// Cache storage
let vimeoCache: VimeoCache | null = null;

/**
 * Determines category based on video title
 */
function determineCategory(title: string): string {
  const lowercaseTitle = title.toLowerCase();
  
  for (const [keyword, category] of Object.entries(categoryKeywords)) {
    if (lowercaseTitle.includes(keyword)) {
      return category;
    }
  }
  
  return 'Uncategorized';
}

/**
 * Format duration from seconds to MM:SS
 */
function formatDuration(seconds: number): string {
  const minutes = Math.floor(seconds / 60);
  const secs = seconds % 60;
  return `${minutes}:${secs < 10 ? '0' : ''}${secs}`;
}

/**
 * Formats date in Month DD, YYYY format
 */
function formatDate(dateString: string): string {
  const date = new Date(dateString);
  return date.toLocaleDateString('en-US', {
    month: 'long',
    day: 'numeric',
    year: 'numeric'
  });
}

/**
 * Fetch video metadata from Vimeo oEmbed API
 */
async function fetchVideoMetadata(videoId: string): Promise<any> {
  const url = `https://vimeo.com/api/oembed.json?url=https://vimeo.com/${videoId}`;
  const response = await fetch(url);
  
  if (!response.ok) {
    throw new Error(`Failed to fetch video metadata for ${videoId}, status: ${response.status}`);
  }
  
  return await response.json();
}

/**
 * Process a collection of videos from configuration
 */
async function processVideos(videoIds: string[], collectionName?: string): Promise<VideoData[]> {
  const processedVideos: VideoData[] = [];
  
  for (const videoId of videoIds) {
    try {
      const metadata = await fetchVideoMetadata(videoId);
      
      const video: VideoData = {
        id: videoId,
        title: metadata.title,
        description: metadata.description || `Growlink video tutorial`,
        thumbnail: metadata.thumbnail_url || '',
        duration: metadata.duration ? formatDuration(metadata.duration) : '0:00',
        category: determineCategory(metadata.title),
        date: metadata.upload_date ? formatDate(metadata.upload_date) : 'Unknown date',
        vimeoId: videoId,
        collection: collectionName
      };
      
      processedVideos.push(video);
    } catch (error) {
      console.error(`Error processing video ${videoId}: ${error}`);
    }
  }
  
  return processedVideos;
}

/**
 * Fetch videos from the Vimeo API
 * This is the main function that should be called externally
 */
export async function fetchVideos(forceRefresh = false): Promise<VideoData[]> {
  // Check if we have cached data and it's still valid
  const now = Date.now();
  
  if (
    !forceRefresh && 
    vimeoCache && 
    vimeoCache.videos.length > 0 && 
    now < vimeoCache.expiresAt
  ) {
    console.log('Using cached video data');
    return vimeoCache.videos;
  }
  
  // Define collections with video IDs
  // In a real implementation, this might come from an API or config file
  const collections = [
    {
      name: "Copilot Series",
      videos: ["907487815", "907487660", "968224954"]
    },
    {
      name: "LINKS Ecosystem",
      videos: ["1059077105"]
    },
    {
      name: "Portal 2.0 Series",
      videos: ["907488208", "914823551"]
    },
    {
      name: "Nutrient Delivery Kit",
      videos: ["1007057861", "1007060714", "1007065024", "1007112254"]
    },
    {
      name: "Only Grows",
      videos: ["1006030715", "1010689831"]
    },
    {
      name: "Customer Highlights",
      videos: ["694035367", "651727303", "651723850", "651716570"]
    }
  ];
  
  // Process all collections
  let allVideos: VideoData[] = [];
  let idCounter = 1;
  
  for (const collection of collections) {
    const collectionVideos = await processVideos(collection.videos, collection.name);
    
    // Assign sequential IDs
    const videosWithIds = collectionVideos.map(video => ({
      ...video,
      id: idCounter++.toString()
    }));
    
    allVideos = [...allVideos, ...videosWithIds];
  }
  
  // Update the cache
  vimeoCache = {
    lastFetched: now,
    videos: allVideos,
    expiresAt: now + CACHE_EXPIRATION
  };
  
  return allVideos;
}

/**
 * Check if videos need refresh by comparing with cache
 * Returns true if there are new or updated videos
 */
export async function checkForUpdates(): Promise<boolean> {
  if (!vimeoCache || vimeoCache.videos.length === 0) {
    return true;
  }
  
  const cachedVideoIds = new Set(vimeoCache.videos.map(v => v.vimeoId));
  
  // Fetch new data
  const freshVideos = await fetchVideos(true);
  const freshVideoIds = new Set(freshVideos.map(v => v.vimeoId));
  
  // Check if any new videos exist
  for (const id of freshVideoIds) {
    if (!cachedVideoIds.has(id)) {
      return true;
    }
  }
  
  // Check for changes in existing videos (titles, etc.)
  for (const freshVideo of freshVideos) {
    const cachedVideo = vimeoCache.videos.find(v => v.vimeoId === freshVideo.vimeoId);
    if (cachedVideo) {
      if (
        cachedVideo.title !== freshVideo.title ||
        cachedVideo.description !== freshVideo.description ||
        cachedVideo.thumbnail !== freshVideo.thumbnail
      ) {
        return true;
      }
    }
  }
  
  return false;
}

/**
 * Get time remaining before cache expiration (in ms)
 */
export function getCacheTimeRemaining(): number {
  if (!vimeoCache) return 0;
  
  const now = Date.now();
  return Math.max(0, vimeoCache.expiresAt - now);
}

/**
 * Force a cache refresh
 */
export async function refreshCache(): Promise<VideoData[]> {
  return await fetchVideos(true);
}