/**
 * StaticThumbnail.tsx
 * Creates a visually consistent thumbnail for videos when the actual thumbnail
 * is not available or when a consistent style is preferred.
 */

import React from 'react';
import { Play } from 'lucide-react';

interface StaticThumbnailProps {
  title: string;
  subtitle?: string;
  collection?: string;
  category: string;
  duration: string;
  className?: string;
}

// Color mapping for backgrounds based on collection or category
const COLORS: Record<string, string> = {
  // Collections
  'Copilot Series': 'from-emerald-800 to-emerald-950',
  'LINKS Ecosystem': 'from-orange-700 to-orange-950',
  'Nutrient Delivery Kit': 'from-yellow-600 to-yellow-900',
  'Portal 2.0 Series': 'from-blue-700 to-blue-950',
  'Customer Highlights': 'from-purple-700 to-purple-950',
  'Only Grows': 'from-red-600 to-red-900',
  
  // Categories
  'Crop Steering': 'from-emerald-700 to-emerald-900',
  'Setup & Installation': 'from-gray-700 to-gray-900',
  'Climate Control': 'from-blue-600 to-blue-900',
  'Lighting Control': 'from-amber-600 to-amber-900',
  'Irrigation & Fertigation': 'from-cyan-700 to-cyan-900',
  'Product Overviews': 'from-violet-700 to-violet-900',
  'Customer Stories': 'from-pink-700 to-pink-900',
  'Podcast': 'from-red-600 to-red-900',
};

const getBackgroundColor = (collection?: string, category?: string): string => {
  if (collection && COLORS[collection]) {
    return COLORS[collection];
  }
  if (category && COLORS[category]) {
    return COLORS[category];
  }
  return 'from-gray-700 to-gray-900';
};

// Extract main title and subtitle
const parseTitle = (title: string): { main: string; sub: string } => {
  const main = title.split(/[:|-]/)[0].trim();
  const sub = title.replace(main, '').replace(/^[:|-]/, '').trim();
  return { main, sub };
};

export default function StaticThumbnail({ 
  title, 
  subtitle, 
  collection, 
  category, 
  duration,
  className = '' 
}: StaticThumbnailProps) {
  const { main, sub } = parseTitle(title);
  const displaySubtitle = subtitle || sub;
  const bgColor = getBackgroundColor(collection, category);

  return (
    <div 
      className={`relative aspect-video overflow-hidden rounded-lg ${className}`}
    >
      {/* Background gradient */}
      <div className={`absolute inset-0 bg-gradient-to-br ${bgColor}`}></div>
      
      {/* Decorative elements */}
      <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,')] bg-center opacity-20"></div>
      <div className="absolute top-0 right-0 w-32 h-32 bg-white opacity-5 rounded-full -mr-10 -mt-10"></div>
      <div className="absolute bottom-0 left-0 w-32 h-32 bg-black opacity-10 rounded-full -ml-10 -mb-10"></div>
      
      {/* Logo watermark */}
      <div className="absolute bottom-3 right-3 text-white/30 text-xs font-light">
        growlink.ag
      </div>
      
      {/* Chevron design element */}
      <div className="absolute top-0 left-0 w-full">
        <svg viewBox="0 0 100 20" className="w-full opacity-20">
          <path d="M0 20 L50 0 L100 20" fill="none" stroke="white" strokeWidth="1" />
          <path d="M0 15 L50 0 L100 15" fill="none" stroke="white" strokeWidth="0.5" />
        </svg>
      </div>
      
      {/* Content area */}
      <div className="absolute inset-0 p-6 flex flex-col justify-center">
        <h3 className="text-xl md:text-2xl font-bold text-white z-10 mb-1">{main}</h3>
        {displaySubtitle && (
          <p className="text-white/80 text-sm md:text-base z-10">{displaySubtitle}</p>
        )}
      </div>
      
      {/* Play button */}
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="flex h-14 w-14 items-center justify-center rounded-full bg-white/90 text-black">
          <Play className="h-6 w-6 fill-current" />
        </div>
      </div>
      
      {/* Duration badge */}
      <div className="absolute bottom-2 right-2 rounded bg-black/70 px-2 py-1 text-xs text-white">
        {duration}
      </div>
    </div>
  );
}