/**
 * VideoModal.tsx
 * Modal component that displays a Vimeo video with its metadata
 */

import React, { useRef, useEffect, useState } from 'react';
import { X, Play } from 'lucide-react';
import { VideoData } from '../../services/VimeoService';
import StaticThumbnail from './StaticThumbnail';

interface VideoModalProps {
  video: VideoData | null;
  onClose: () => void;
}

export default function VideoModal({ video, onClose }: VideoModalProps) {
  const modalRef = useRef<HTMLDivElement>(null);
  const [isPlayerReady, setIsPlayerReady] = useState(false);
  const [playClicked, setPlayClicked] = useState(false);

  useEffect(() => {
    function handleEscape(e: KeyboardEvent) {
      if (e.key === 'Escape') onClose();
    }
    
    function handleClickOutside(e: MouseEvent) {
      if (modalRef.current && !modalRef.current.contains(e.target as Node)) {
        onClose();
      }
    }
    
    document.addEventListener('keydown', handleEscape);
    document.addEventListener('mousedown', handleClickOutside);
    
    // Prevent scrolling on the body when modal is open
    document.body.style.overflow = 'hidden';
    
    // Load the Vimeo Player API if it's not already loaded
    if (typeof window !== 'undefined' && !window.Vimeo) {
      const script = document.createElement('script');
      script.src = 'https://player.vimeo.com/api/player.js';
      script.async = true;
      script.onload = () => setIsPlayerReady(true);
      document.body.appendChild(script);
    } else {
      setIsPlayerReady(true);
    }
    
    return () => {
      document.removeEventListener('keydown', handleEscape);
      document.removeEventListener('mousedown', handleClickOutside);
      document.body.style.overflow = '';
    };
  }, [onClose]);

  if (!video) return null;
  
  const handlePlayClick = () => {
    setPlayClicked(true);
  };

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-75 backdrop-blur-sm">
      <div 
        ref={modalRef}
        className="relative mx-auto max-w-5xl w-full rounded-2xl bg-white shadow-xl"
      >
        <button
          onClick={onClose}
          className="absolute -right-4 -top-4 z-10 flex h-10 w-10 items-center justify-center rounded-full bg-white text-gray-500 shadow-md transition-colors hover:text-black focus:outline-none"
        >
          <X className="h-5 w-5" />
        </button>
        
        <div className="aspect-video w-full overflow-hidden rounded-t-2xl">
          {!playClicked ? (
            // Use a custom thumbnail with play button
            <div className="relative w-full h-full cursor-pointer" onClick={handlePlayClick}>
              <StaticThumbnail 
                title={video.title}
                category={video.category}
                collection={video.collection}
                duration={video.duration}
                className="w-full h-full"
              />
              
              {/* Large play button overlay */}
              <div className="absolute inset-0 flex items-center justify-center cursor-pointer group">
                <div className="flex h-20 w-20 items-center justify-center rounded-full bg-white bg-opacity-90 text-black shadow-lg transition-transform duration-300 group-hover:scale-110">
                  <Play className="h-8 w-8 fill-current" />
                </div>
              </div>
            </div>
          ) : (
            // Load the iframe player once play is clicked
            <iframe
              src={`https://player.vimeo.com/video/${video.vimeoId}?autoplay=1&title=0&byline=0&portrait=0`}
              className="h-full w-full rounded-t-2xl"
              allow="autoplay; fullscreen; picture-in-picture; clipboard-write; encrypted-media"
              allowFullScreen
              title={video.title}
            />
          )}
        </div>
        
        <div className="p-6">
          <h2 className="text-2xl font-medium text-gray-900">{video.title}</h2>
          <p className="mt-2 text-gray-600">{video.description}</p>
          
          <div className="mt-4 flex flex-wrap gap-3 items-center justify-between">
            <div className="flex flex-wrap gap-2">
              <span className="rounded-full bg-gray-100 px-3 py-1 text-sm font-medium text-gray-800">
                {video.category}
              </span>
              {video.collection && (
                <span className="rounded-full bg-blue-50 px-3 py-1 text-sm font-medium text-blue-700">
                  {video.collection}
                </span>
              )}
            </div>
            <span className="text-sm text-gray-500">{video.date}</span>
          </div>
        </div>
      </div>
    </div>
  );
}