/**
 * webflow-university.jsx
 * Standalone entry point for the University page when embedded in Webflow
 */

import React from 'react';
import { createRoot } from 'react-dom/client';
import UniversityPage from './components/university/UniversityPage';

// Global export for UMD builds
if (typeof window !== 'undefined') {
  window.GrowlinkUniversity = UniversityPage;
}

// Auto-initialize if the root element exists
if (typeof document !== 'undefined') {
  const rootElement = document.getElementById('growlink-university-root');
  if (rootElement) {
    const root = createRoot(rootElement);
    root.render(<UniversityPage />);
  }
}