/**
 * useVideos.tsx
 * Custom hook for managing Vimeo video data with auto-refresh functionality
 */

import { useState, useEffect, useCallback } from 'react';
import { fetchVideos, VideoData, checkForUpdates, getCacheTimeRemaining, refreshCache } from '../services/VimeoService';

interface UseVideosOptions {
  /**
   * Should videos be automatically refreshed periodically?
   * Default: true
   */
  autoRefresh?: boolean;
  
  /**
   * How often to check for updates (in milliseconds)
   * Default: 5 minutes (300000ms)
   */
  refreshInterval?: number;
}

export default function useVideos(options: UseVideosOptions = {}) {
  const { 
    autoRefresh = true,
    refreshInterval = 5 * 60 * 1000, // 5 minutes
  } = options;
  
  const [videos, setVideos] = useState<VideoData[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<Error | null>(null);
  const [lastRefreshed, setLastRefreshed] = useState<Date | null>(null);
  
  // Manual refresh function
  const refreshVideos = useCallback(async () => {
    setLoading(true);
    setError(null);
    
    try {
      const freshVideos = await refreshCache();
      setVideos(freshVideos);
      setLastRefreshed(new Date());
    } catch (err) {
      setError(err instanceof Error ? err : new Error('Failed to refresh videos'));
      console.error('Error refreshing videos:', err);
    } finally {
      setLoading(false);
    }
  }, []);
  
  // Initial load function
  const loadVideos = useCallback(async () => {
    setLoading(true);
    setError(null);
    
    try {
      const data = await fetchVideos();
      setVideos(data);
      setLastRefreshed(new Date());
    } catch (err) {
      setError(err instanceof Error ? err : new Error('Failed to load videos'));
      console.error('Error loading videos:', err);
    } finally {
      setLoading(false);
    }
  }, []);
  
  // Check for updates without forcing a full refresh
  const checkUpdates = useCallback(async () => {
    try {
      const hasUpdates = await checkForUpdates();
      if (hasUpdates) {
        await refreshVideos();
      }
    } catch (err) {
      console.error('Error checking for video updates:', err);
    }
  }, [refreshVideos]);
  
  // Initial load
  useEffect(() => {
    loadVideos();
  }, [loadVideos]);
  
  // Set up periodic refresh
  useEffect(() => {
    if (!autoRefresh) return;
    
    // Set up interval for checking updates
    const intervalId = setInterval(checkUpdates, refreshInterval);
    
    return () => clearInterval(intervalId);
  }, [autoRefresh, refreshInterval, checkUpdates]);
  
  // Calculate time to next refresh
  const timeToNextRefresh = useCallback(() => {
    const cacheTimeRemaining = getCacheTimeRemaining();
    
    // If cache is still valid, return the shorter of:
    // 1. Time until cache expiration
    // 2. Time until next scheduled check
    if (cacheTimeRemaining > 0 && lastRefreshed) {
      const timeSinceLastRefresh = Date.now() - lastRefreshed.getTime();
      const timeUntilNextCheck = Math.max(0, refreshInterval - timeSinceLastRefresh);
      
      return Math.min(cacheTimeRemaining, timeUntilNextCheck);
    }
    
    // If cache is expired or no last refresh time, return time until next check
    return refreshInterval;
  }, [lastRefreshed, refreshInterval]);
  
  return {
    videos,
    loading,
    error,
    refreshVideos,
    lastRefreshed,
    timeToNextRefresh
  };
}